:root {
  --primary-color: #3498db;
  --secondary-color: #2c3e50;
  --accent-color: #e74c3c;
  --text-color: #333;
  --light-bg: #f5f5f5;
  --border-color: #ddd;
  --success-color: #27ae60;
  --male-color: #3498db;
  --female-color: #e74c3c;
  --unknown-color: #95a5a6;
  --node-bg: #fff;
  --node-border: #ddd;
  --link-color: #95a5a6;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: var(--text-color);
  background-color: var(--light-bg);
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.6;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

header {
  border-bottom: 1px solid var(--border-color);
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding: 20px 0;
  display: flex;
}

h1 {
  color: var(--secondary-color);
  font-size: 2rem;
}

.language-selector {
  align-items: center;
  gap: 10px;
  display: flex;
}

select {
  border: 1px solid var(--border-color);
  background-color: #fff;
  border-radius: 4px;
  padding: 8px 12px;
  font-size: 14px;
}

main {
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  display: grid;
}

.input-section {
  background-color: #fff;
  border-radius: 8px;
  grid-column: 1 / -1;
  padding: 20px;
  box-shadow: 0 2px 10px #0000000d;
}

.file-input {
  margin: 20px 0;
}

input[type="file"] {
  border: 1px dashed var(--border-color);
  border-radius: 4px;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
  display: block;
}

.sample-data {
  margin-top: 15px;
}

button {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 10px 16px;
  font-size: 14px;
  transition: background-color .3s;
}

button:hover {
  background-color: #2980b9;
}

.configuration-section {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 2px 10px #0000000d;
}

.config-group {
  margin-bottom: 25px;
}

h2, h3 {
  color: var(--secondary-color);
  margin-bottom: 15px;
}

.config-item {
  margin-bottom: 15px;
}

label {
  margin-bottom: 5px;
  font-weight: 500;
  display: block;
}

input[type="range"], input[type="number"] {
  border: 1px solid var(--border-color);
  border-radius: 4px;
  width: 100%;
  padding: 8px;
}

.visualization-section {
  background-color: #fff;
  border-radius: 8px;
  grid-column: 1 / -1;
  padding: 20px;
  box-shadow: 0 2px 10px #0000000d;
}

.controls {
  gap: 10px;
  margin-bottom: 20px;
  display: flex;
}

#visualization-container {
  border: 1px solid var(--border-color);
  width: 100%;
  height: 600px;
  margin-top: 20px;
  position: relative;
  overflow: auto;
}

svg {
  margin: 0 auto;
  display: block;
}

.node {
  cursor: pointer;
}

.node rect {
  fill: var(--node-bg);
  stroke: var(--node-border);
  stroke-width: 1px;
  rx: 5;
  ry: 5;
}

.node-male rect {
  stroke: var(--male-color);
}

.node-female rect {
  stroke: var(--female-color);
}

.node-unknown rect {
  stroke: var(--unknown-color);
}

.node text {
  text-anchor: middle;
  dominant-baseline: middle;
  font-size: 14px;
}

.link {
  fill: none;
  stroke: var(--link-color);
  stroke-width: 1.5px;
}

.link-marriage {
  stroke-dasharray: 5;
}

footer {
  text-align: center;
  border-top: 1px solid var(--border-color);
  color: #777;
  margin-top: 50px;
  padding: 20px 0;
}

.tamil {
  font-family: Noto Sans Tamil, sans-serif;
}

@media screen and (width <= 768px) {
  main {
    grid-template-columns: 1fr;
  }

  header {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }

  .controls {
    flex-wrap: wrap;
  }
}
